import Vue from 'vue';
import {fetchCode} from './fetch';

/**
 * @getCode function from the wxURL:
 * eg. url: http://handhrhap.hand-china.com/dist/?code=zCGiYN30SxNDeyN7yLn2n3iPC8mDPi0ZigeHDWL3L7U&state=STATE
 * urlcode
 *
 *
 * @ddConfig function
 * //dd.config
 */

let DD_CONFIGS = {
  getCode() {
    let ddUrlObj = {};
    let urlParamsArray = window.location.search.substring(1).split('&');

    urlParamsArray.forEach((item, index, array) => {
      let pos = item.indexOf('=');
      if (pos == '-1') {
      } else {
        ddUrlObj[item.split('=')[0]] = item.split('=')[1];
      }
    });

    return ddUrlObj.code;
  },

  ddConfig(ddUrl) {
    fetchCode({
      url: process.env.baseUrl + 'api/public/getDdJSSDKParams',
      method: 'post',
      data: {
        url: ddUrl
      }
    }).then(function (res) {
      const data = res.data.rows[0] || [];
      window.localStorage.setItem('corpId', data.appId);
      dd.config({
        agentId: "115509997", // ID
        corpId: data.appId,//ID
        timeStamp: data.timestamp, // 
        nonceStr: data.nonceStr, // 
        signature: data.signature, // 
        type: 0,   //0jsapi,1jsapi0dingtalk.js0.8.3
        jsApiList: ['device.geolocation.get','runtime.permission.requestAuthCode','biz.util.uploadImage'] // jsapidd
      });
    });
  }
};

export default DD_CONFIGS;





